import React, { useEffect, useState } from 'react';
import { AuthProvider } from './contexts/AuthContext';
import { LanguageProvider } from './contexts/LanguageContext';
import { Toaster } from './components/ui/sonner';
import { Button } from './components/ui/button';
import { Card } from './components/ui/card';
import { ScrollArea } from './components/ui/scroll-area';
import { Input } from './components/ui/input';
import { Avatar } from './components/ui/avatar';
import { 
  Bot, Send, User, Sparkles, MessageCircle, 
  BookOpen, Trophy, BarChart3, Users, 
  Settings, LogOut, Home, Globe, Menu, X,
  Brain, Target, Zap, TrendingUp, LineChart
} from 'lucide-react';
import { useAuth } from './contexts/AuthContext';
import { useLanguage } from './contexts/LanguageContext';
import { dataService } from './utils/dataService';
import { Auth } from './components/pages/Auth';
import { Tests } from './components/pages/Tests';
import { Profile } from './components/pages/Profile';
import { Investor } from './components/pages/Investor';

interface Message {
  id: string;
  content: string;
  role: 'user' | 'assistant' | 'system';
  timestamp: Date;
  isTyping?: boolean;
  actions?: Array<{
    label: string;
    action: () => void;
  }>;
}

function App() {
  const { user, logout, isLoading: authLoading } = useAuth();
  const { language, setLanguage, t } = useLanguage();
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [currentView, setCurrentView] = useState<'chat' | 'tests' | 'profile' | 'auth' | 'investor'>('chat');
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const messagesEndRef = React.useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Initialize with welcome message
    setMessages([
      {
        id: '1',
        content: `    EduPlatform!
        
   -    .  :
        
    
     
      

  ?`,
        role: 'assistant',
        timestamp: new Date(),
        actions: [
          { label: '  ', action: () => setCurrentView('tests') },
          { label: '  ', action: () => setCurrentView('profile') },
          { label: '   ', action: () => setInputValue('     ') }
        ]
      }
    ]);

    // Initialize backend
    const initializeBackend = async () => {
      try {
        const response = await fetch('/api/init', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' }
        });
        if (response.ok) {
          console.log('Backend initialized');
        }
      } catch (error) {
        // Silent in dev
      }
    };
    
    initializeBackend();
  }, []);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const sendMessage = async () => {
    if (!inputValue.trim() || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content: inputValue.trim(),
      role: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);

    // Add typing indicator
    const typingMessage: Message = {
      id: 'typing',
      content: '...',
      role: 'assistant',
      timestamp: new Date(),
      isTyping: true
    };
    setMessages(prev => [...prev, typingMessage]);

    try {
      // Check for commands
      const lowerInput = userMessage.content.toLowerCase();
      let response = '';
      let actions: Message['actions'] = undefined;

      if (lowerInput.includes('') || lowerInput.includes('')) {
        response = '!   .     .';
        actions = [
          { label: '   ', action: () => setCurrentView('tests') }
        ];
      } else if (lowerInput.includes('') || lowerInput.includes('')) {
        response = '     .';
        actions = [
          { label: '  ', action: () => setCurrentView('profile') }
        ];
      } else {
        // AI response
        const context = user ? `
          : ${user.name} (${user.role})
           : ${user.tests_completed || 0}
           : ${user.average_score || 0}%
        ` : '';

        const { response: aiResponse } = await dataService.sendAIMessage(
          userMessage.content,
          context,
          language as 'ru' | 'kz'
        );
        response = aiResponse;
      }

      // Remove typing and add response
      setMessages(prev => {
        const filtered = prev.filter(msg => msg.id !== 'typing');
        return [...filtered, {
          id: Date.now().toString(),
          content: response,
          role: 'assistant',
          timestamp: new Date(),
          actions
        }];
      });
    } catch (error) {
      console.error('Error:', error);
      setMessages(prev => {
        const filtered = prev.filter(msg => msg.id !== 'typing');
        return [...filtered, {
          id: Date.now().toString(),
          content: ',  .  .',
          role: 'assistant',
          timestamp: new Date()
        }];
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const handleLogout = async () => {
    await logout();
    setCurrentView('chat');
  };

  // Quick actions for chat
  const quickActions = [
    { icon: '', label: ' ', action: () => setCurrentView('tests') },
    { icon: '', label: ' ', action: () => setCurrentView('profile') },
    { icon: '', label: '', action: () => setInputValue('     ') },
    { icon: '', label: ' ', action: () => setInputValue('    ') }
  ];

  if (authLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-white">...</p>
        </div>
      </div>
    );
  }

  // If not authenticated and trying to access protected views
  if (!user && (currentView === 'tests' || currentView === 'profile')) {
    setCurrentView('auth');
  }

  return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 text-white flex">
          {/* Mobile Overlay */}
          {sidebarOpen && (
            <div 
              className="fixed inset-0 bg-black/50 z-40 lg:hidden"
              onClick={() => setSidebarOpen(false)}
            />
          )}
          
          {/* Sidebar */}
          <div className={`fixed inset-y-0 left-0 z-50 w-64 bg-black/50 backdrop-blur-lg border-r border-white/10 transform transition-transform duration-300 ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'} lg:translate-x-0 lg:static lg:w-64 lg:flex-shrink-0`}>
            <div className="flex flex-col h-full">
              {/* Logo */}
              <div className="p-6 border-b border-white/10">
                <div className="flex items-center gap-3">
                  <div className="relative">
                    <BookOpen className="h-8 w-8 text-blue-400" />
                    <Sparkles className="absolute -top-1 -right-1 h-4 w-4 text-yellow-400 animate-pulse" />
                  </div>
                  <div>
                    <h1 className="text-xl font-bold bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
                      EduPlatform
                    </h1>
                    <p className="text-xs text-gray-400">Next-Gen Learning</p>
                  </div>
                </div>
              </div>

              {/* Navigation */}
              <nav className="flex-1 p-4 space-y-2">
                <button
                  onClick={() => setCurrentView('chat')}
                  className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                    currentView === 'chat' 
                      ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30' 
                      : 'hover:bg-white/5 text-gray-300'
                  }`}
                >
                  <MessageCircle className="h-5 w-5" />
                  <span> </span>
                </button>

                <button
                  onClick={() => setCurrentView('tests')}
                  className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                    currentView === 'tests' 
                      ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30' 
                      : 'hover:bg-white/5 text-gray-300'
                  }`}
                >
                  <Brain className="h-5 w-5" />
                  <span></span>
                </button>

                {user && (
                  <button
                    onClick={() => setCurrentView('profile')}
                    className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                      currentView === 'profile' 
                        ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30' 
                        : 'hover:bg-white/5 text-gray-300'
                    }`}
                  >
                    <BarChart3 className="h-5 w-5" />
                    <span></span>
                  </button>
                )}
                {/* Investor dashboard (demo) */}
                <button
                  onClick={() => setCurrentView('investor')}
                  className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                    currentView === 'investor' 
                      ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30' 
                      : 'hover:bg-white/5 text-gray-300'
                  }`}
                >
                  <LineChart className="h-5 w-5" />
                  <span>Investor</span>
                </button>
              </nav>

              {/* User section */}
              <div className="p-4 border-t border-white/10">
                {user ? (
                  <div className="space-y-3">
                    <div className="flex items-center gap-3 px-3 py-2 bg-white/5 rounded-lg">
                      <Avatar className="h-8 w-8 bg-gradient-to-r from-blue-500 to-purple-500">
                        <User className="h-4 w-4 text-white" />
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium truncate">{user.name}</p>
                        <p className="text-xs text-gray-400">{user.email}</p>
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => setLanguage(language === 'ru' ? 'kz' : 'ru')}
                        className="flex-1 flex items-center justify-center gap-2 px-3 py-2 bg-white/5 rounded-lg hover:bg-white/10 transition-colors"
                      >
                        <Globe className="h-4 w-4" />
                        <span className="text-sm">{language.toUpperCase()}</span>
                      </button>
                      <button
                        onClick={handleLogout}
                        className="flex-1 flex items-center justify-center gap-2 px-3 py-2 bg-white/5 rounded-lg hover:bg-white/10 transition-colors"
                      >
                        <LogOut className="h-4 w-4" />
                        <span className="text-sm"></span>
                      </button>
                    </div>
                  </div>
                ) : (
                  <button
                    onClick={() => setCurrentView('auth')}
                    className="w-full flex items-center justify-center gap-2 px-4 py-3 bg-gradient-to-r from-blue-500 to-purple-500 rounded-lg hover:from-blue-600 hover:to-purple-600 transition-all"
                  >
                    <User className="h-5 w-5" />
                    <span></span>
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* Mobile menu button */}
          <button
            onClick={() => setSidebarOpen(!sidebarOpen)}
            className="lg:hidden fixed top-4 left-4 z-50 p-2 bg-black/50 backdrop-blur-lg rounded-lg border border-white/10"
          >
            {sidebarOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
          </button>

          {/* Main Content */}
          <div className="flex-1 flex flex-col lg:ml-0 min-w-0">
            {currentView === 'chat' ? (
              <>
                {/* Chat Header */}
                <div className="bg-black/30 backdrop-blur-lg border-b border-white/10 px-6 py-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <div className="relative">
                        <Bot className="h-8 w-8 text-blue-400" />
                        <div className="absolute -bottom-1 -right-1 w-3 h-3 bg-green-400 rounded-full animate-pulse"></div>
                      </div>
                      <div>
                        <h2 className="text-lg font-semibold"> </h2>
                        <p className="text-sm text-gray-400">  </p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      {quickActions.map((action, index) => (
                        <button
                          key={index}
                          onClick={action.action}
                          className="px-3 py-1.5 bg-white/5 hover:bg-white/10 rounded-lg text-sm transition-colors flex items-center gap-2"
                          title={action.label}
                        >
                          <span>{action.icon}</span>
                          <span className="hidden sm:inline">{action.label}</span>
                        </button>
                      ))}
                    </div>
                  </div>
                </div>

                {/* Messages */}
                <ScrollArea className="flex-1 p-6">
                  <div className="max-w-4xl mx-auto space-y-4">
                    {messages.map((message) => (
                      <div
                        key={message.id}
                        className={`flex gap-3 ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
                      >
                        {message.role === 'assistant' && (
                          <Avatar className="h-8 w-8 bg-gradient-to-r from-blue-500 to-purple-500">
                            <Bot className="h-4 w-4 text-white" />
                          </Avatar>
                        )}
                        <div className={`max-w-[70%] ${message.role === 'user' ? 'order-1' : ''}`}>
                          <div
                            className={`rounded-2xl px-4 py-3 ${
                              message.role === 'user'
                                ? 'bg-gradient-to-r from-blue-500 to-purple-500 text-white'
                                : 'bg-white/10 backdrop-blur-lg border border-white/10'
                            }`}
                          >
                            {message.isTyping ? (
                              <div className="flex gap-1">
                                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0ms' }} />
                                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '150ms' }} />
                                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '300ms' }} />
                              </div>
                            ) : (
                              <p className="whitespace-pre-wrap">{message.content}</p>
                            )}
                          </div>
                          {message.actions && (
                            <div className="mt-2 flex flex-wrap gap-2">
                              {message.actions.map((action, index) => (
                                <button
                                  key={index}
                                  onClick={action.action}
                                  className="px-3 py-1.5 bg-blue-500/20 hover:bg-blue-500/30 text-blue-400 rounded-lg text-sm transition-colors"
                                >
                                  {action.label}
                                </button>
                              ))}
                            </div>
                          )}
                        </div>
                        {message.role === 'user' && (
                          <Avatar className="h-8 w-8 bg-gradient-to-r from-green-500 to-teal-500 order-2">
                            <User className="h-4 w-4 text-white" />
                          </Avatar>
                        )}
                      </div>
                    ))}
                    <div ref={messagesEndRef} />
                  </div>
                </ScrollArea>

                {/* Input */}
                <div className="bg-black/30 backdrop-blur-lg border-t border-white/10 p-6">
                  <div className="max-w-4xl mx-auto">
                    <div className="flex gap-3">
                      <Input
                        value={inputValue}
                        onChange={(e) => setInputValue(e.target.value)}
                        onKeyPress={handleKeyPress}
                        placeholder="    ..."
                        disabled={isLoading}
                        className="flex-1 bg-white/10 border-white/20 text-white placeholder-gray-400 focus:border-blue-400"
                      />
                      <Button
                        onClick={sendMessage}
                        disabled={isLoading || !inputValue.trim()}
                        className="bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 px-6"
                      >
                        <Send className="h-5 w-5" />
                      </Button>
                    </div>
                    {/* Suggestions */}
                    {!isLoading && inputValue.length === 0 && (
                      <div className="mt-3 flex flex-wrap gap-2">
                        {[
                          '  ?',
                          '  ',
                          '  ',
                          '  '
                        ].map((suggestion, index) => (
                          <button
                            key={index}
                            onClick={() => setInputValue(suggestion)}
                            className="px-3 py-1.5 bg-white/5 hover:bg-white/10 rounded-lg text-sm text-gray-300 transition-colors"
                          >
                            {suggestion}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </>
            ) : currentView === 'auth' ? (
              <div className="flex-1 flex items-center justify-center p-6">
                <Auth />
              </div>
            ) : currentView === 'tests' ? (
              <div className="flex-1 overflow-auto">
                <Tests />
              </div>
            ) : currentView === 'profile' ? (
              <div className="flex-1 overflow-auto">
                <Profile />
              </div>
            ) : currentView === 'investor' ? (
              <div className="flex-1 overflow-auto">
                <Investor />
              </div>
            ) : null}
          </div>

          <Toaster 
            position="top-right" 
            expand={true}
            richColors={true}
            toastOptions={{
              style: {
                background: 'rgba(0, 0, 0, 0.8)',
                backdropFilter: 'blur(10px)',
                border: '1px solid rgba(255, 255, 255, 0.1)',
                color: 'white'
              }
            }}
          />
        </div>
  );
}

export default App;